.Navbar {
  background-color: #DF5757;
  box-shadow: 0px 2px 5px rgba(0,0,0,0.75)
}
.Navbar-brand {
  font-weight: bold;
  font-size: 1.5em;
  border: none;
}
.Navbar-link-container {
  max-width: 900px;
  margin: auto;
  padding: 0.5em 0;
}
.Navbar-link {
  display: inline-block;
  padding: 0 1em;
  text-decoration: none;
  color: inherit;
  border-left: 1px solid #5C0318;
}

.Navbar-link:first-child {
  border: none;
  padding-left: 0;
}

@media only screen and (max-width: 950px) {
  .Navbar{
    text-align: center;
  }
  #bioLink {
    display: none;
  }
}

@media only screen and (max-width: 504px) {
  .Navbar {
    padding: 0.5em 0;
  }
}

@media only screen and (max-width: 357px) {
  .Navbar {
    display: flex;
    flex-direction: column;
  }
  .Navbar-link {
    padding: 0.5em;
  }
}