.Portfolio{
  max-width: 900px;
  margin: auto;
}
.Portfolio-projects{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.Portfolio-descriptor{
  padding: 1em 0;
  padding-bottom: 0;
}
.Portfolio-descriptor a{
  text-decoration: underline;
  color: inherit;
}

@media only screen and (max-width: 950px){
  .Portfolio{
    padding: .5em;
  }
}