.LandingPage {
  padding-top: .5em;
  max-width: 900px;
  margin: auto;
}
#profileImage {
  width: 33%;
  float: left;
  margin: 0 1em .5em 0;
  box-shadow: 0px 2px 5px rgba(0,0,0,0.75)
}
#LandingPage-aboutMe {
  border-bottom: 1px solid#5C0318;
  overflow: hidden;
}
.LandingPage-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.LandingPage-linkGroup {
  margin: 1%;
  text-align: center;
}
.LandingPage-linkGroup a {
  color: inherit;
  text-decoration: none;
}
.LandingPage-clickable {
  overflow: hidden;
  font-size: 5em;
  margin: auto;
}

@media only screen and (max-width: 1000px) {
  #profileImage {
    width: 50%;
  }
}
@media only screen and (max-width: 550px) {
  .LandingPage{
    display: flex;
    flex-direction: column;
    padding: .5em;
    text-align: center;
  }
  #profileImage {
    width: 90%;
    margin: auto;
  }
}
@media only screen and (max-width: 575px){
  .LandingPage-links{
    flex-direction: column;
  }
}
@media only screen and (max-width: 504px) {
  .LandingPage {
    width: 100%;
  }
  .LandingPage-clickable {
    font-size: 2em;
  }
}
